#enchant-container {
  height: 680px;
  width: 920px;
  flex-wrap: wrap;
  align-items: center;
  display: flex;
  justify-content: center;
  background-image: url(https://raw.githubusercontent.com/BartoszSeno/ClickerZero/main/src/assets/MainImg/enchant.png);
  position: relative;
  background-repeat: no-repeat;
  z-index: 100;
}

.encahnt-box {
  display: flex;
  height: 500px;
  width: 300px;
  color: white;
  justify-content: space-around;
  align-items: center;
  flex-direction: column-reverse;
}

.putItemThere,
.EnchantSuccess {
  display: flex;
  justify-content: center;
  align-items: center;
}
.putItemThere {
  position: absolute;
  margin-top: 360px;
  margin-left: -560px;
  height: 85px;
  width: 190px;
}

.EnchantSuccess {
  position: absolute;
  height: 131px;
  width: 130px;
  margin-left: 170px;
  margin-top: 185px;
}

#EnchantMenuChouseWeapon {
  position: absolute;
  background-color: rgb(0, 0, 0);
  display: flex;
  flex-direction: column;
  margin-left: 500px;
  margin-top: 80px;
  width: 300px;
  height: 350px;
  border: 2px solid white;
  border-radius: 7px;
  overflow: auto;
  overflow-x: hidden;
  z-index: 100;
}

.clickForEnchant {
  position: absolute;
  margin-top: 200px;
}
.EnchantSuccess {
  color: white;
}

.EnchantSuccess:hover + .infoEnchant {
  color: white;
  display: flex;
}

.infoEnchant {
  display: flex;
  flex-direction: column;
  background-color: aqua;
  display: none;
  position: absolute;
  height: 100px;
  width: 200px;
  font-size: 30px;
  margin-left: 580px;
  margin-top: 180px;
}
.UpgradeDmgTitle {
  font-size: 20px;
  color: gray;
}
.UpgradeImg {
  height: 40px;
  width: 40px;
}
.UpgradeName {
  margin-bottom: 10px;
}
.enchantBox2 {
  display: flex;
}

.mainWeaponImg {
  height: 90%;
}

.close {
  display: none !important;
}

.open {
  display: flex;
}

.enchantButton {
  position: absolute;
  margin-left: -550px;
  margin-top: 200px;
}
